import React, { Component } from "react";
import { Navigation } from "./layout";
import { Row, Col, Layout, Icon, Button, Card } from "antd";

import { OverPack } from "rc-scroll-anim";

import TweenOne from "rc-tween-one";
import Animate from "rc-animate";
import QueueAnim from "rc-queue-anim";
const { Header, Footer, Sider, Content } = Layout;
const { Meta } = Card;
var ScrollAnim = require("rc-scroll-anim");
var ScrollOverPack = ScrollAnim.OverPack;
const contentLinks = {
  game4minbingo: "http://35.232.144.160/"
};
export class HomePage extends Component {
  onChange = (a, b, c) => {
    console.log(a, b, c);
  };
  render() {
    return (
      <React.Fragment>
        <div>
          <Navigation />
          <Content className="banner-wrapper">
            <div className="mt-5">
              <Row style={{ display: "flex", flexWrap: "wrap" }}>
                <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                  <div className="banner-image-wrapper">
                    <img
                      src={require("../static/image/banner.jpg")}
                      alt="banner"
                    />
                  </div>
                </Col>
                <Col
                  xs={24}
                  sm={24}
                  md={24}
                  lg={12}
                  xl={12}
                  className="flex-content-center"
                >
                  <QueueAnim key="intro-landing" leaveReverse duration="2000">
                    <div key="intro-1">
                      <img
                        src={require("../static/image/logo.png")}
                        alt=""
                        style={{ width: "300px", height: "51px" }}
                      />
                    </div>
                    <div
                      key="intro-2"
                      className="median-font mt-2 default-color"
                    >
                      <strong>Huynh Nhat Cuong</strong> - Information Website
                    </div>
                    <div key="intro-3" className="social-group mt-3">
                      <a
                        href="https://www.facebook.com/poochi97"
                        target="_blank"
                        rel="facebook"
                      >
                        <Button
                          size="large"
                          type="primary"
                          className="m-horizontal-1 m-vertical-1"
                        >
                          <Icon type="facebook" />
                          <span>FaceBook</span>
                        </Button>
                      </a>
                      <a
                        href="https://www.instagram.com/poochi97/"
                        target="_blank"
                        rel="instagram"
                      >
                        <Button size="large" className="m-horizontal-1 m-vertical-1">
                          <Icon type="instagram" />
                          <span>Instagarm</span>
                        </Button>
                      </a>
                      <a
                        href="https://github.com/ninja020250"
                        target="_blank"
                        rel="github"
                      >
                        <Button size="large" className="m-horizontal-1 m-vertical-1">
                          <Icon type="github" />
                          <span>Github</span>
                        </Button>
                      </a>
                    </div>
                  </QueueAnim>
                </Col>
              </Row>
              <div className="page-content-2 mt-3">
                <div className="title">
                  My<strong> Knowledge</strong>
                </div>
                <LandingPage />
              </div>
            </div>
          </Content>
          <Footer className="mt-3">
            This website Develop by <strong>HUYNH NHAT CUONG</strong>, Powered by{" "}
            <strong>ReactJS </strong>
          </Footer>
        </div>
      </React.Fragment>
    );
  }
}

export class LandingPage extends Component {
  state = {
    cards: [
      {
        image: require("../static/image/web-landing.jpg"),
        title: "Web Develop",
        description: ""
      },
      {
        image: require("../static/image/mobile-landing.jpg"),
        title: "Mobile Develop",
        description: ""
      },
      {
        image: require("../static/image/information-landing.jpg"),
        title: "Information system",
        description: ""
      }
    ]
  };
  render() {
    const { cards } = this.state;
    return (
      <div className="landing-group">
        <Row>
          <OverPack style={{ overflow: "hidden", minHeight: 400 }}>
            <QueueAnim key="card-landing" leaveReverse>
              {cards.map((card, index) => {
                return (
                  <Col
                    xs={24}
                    sm={24}
                    md={12}
                    lg={8}
                    xl={8}
                    key={`card-${index}`}
                  >
                    <div className="m-vertical-1">
                      <Card
                        hoverable
                        cover={<img alt="image" src={card.image} />}
                      >
                        <Meta
                          title={card.title}
                          description={card.description}
                        />
                      </Card>
                      
                    </div>
                  </Col>
                );
              })}
              <Col xs={24} sm={24} md={24} lg={24} xl={24} key={`card-${-1}`}>
                <div className="align-center">
                  Check out my CV <a href="">here</a>
                </div>
              </Col>
            </QueueAnim>
          </OverPack>
        </Row>
      </div>
    );
  }
}
