import React, { Component } from "react";
import { Menu, Icon, Button } from "antd";
export class Navigation extends Component {
  state = {
    current: "home"
  };

  handleClick = e => {
    console.log("click ", e);
    this.setState({
      current: e.key
    });
  };

  render() {
    const { SubMenu } = Menu;
    return (
      <div id="menu-header">
        <Menu
          onClick={this.handleClick}
          selectedKeys={[this.state.current]}
          mode="horizontal"
        >
          <Menu.Item key="home">
            <a href="" target="_blank" rel="noopener noreferrer">
              Trang Chá»§
            </a>
          </Menu.Item>
          <Menu.Item key="blogs">
            <a href="" target="_blank" rel="noopener noreferrer">
              Blog
            </a>
          </Menu.Item>
          <Menu.Item key="cv">
            <a href="" target="_blank" rel="noopener noreferrer">
              cv
            </a>
          </Menu.Item>

          {/* <a
            href="https://www.facebook.com/poochi97"
            target="_blank"
            rel="facebook"
          >
            <Button>
              <Icon type="facebook" /> My FaceBook
            </Button>
          </a> */}
        </Menu>
      </div>
    );
  }
}
